.tb_rp_modal{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    z-index: 9999999;
    opacity: 1;
    font-family: inherit;
    background: rgba(0, 0, 0, 0.5);
    -webkit-transition: opacity 0.3s ease-out 0s;
    -moz-transition: opacity 0.3s ease-out 0s;
    -ms-transition: opacity 0.3s ease-out 0s;
    -o-transition: opacity 0.3s ease-out 0s;
    transition: opacity 0.3s ease-out 0s;
}
.tb_rp_dialog{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
}

.tb_rp_video_wrap_,
.tb_rp_image_wrap_ {
    position: relative;
    padding-bottom: 100vh !important;
}

.tb_rp_image, .tb_rp_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    margin: 0;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_rp_video{
    background-color: #000;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}

.tb_rp_video video{
    object-fit: cover;
    object-position: center;
    height: 100%;
    width: 100%;
}
.tb_rp_video video::-webkit-media-controls-panel {
    background-image: linear-gradient(transparent, transparent) !important;
}

.tb_time_line_wrap{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 4px;
    z-index: 3;
}

.tb_time_line{
    position: absolute;
    left: 0;
    top: 0;
    width: 0%;
    background-color: rgba(255, 255, 255, 0.95);
    height: 100%;
    border-radius: 4px;
}

.tb_rp_mute_btn {
    background: rgba(0, 0, 0, 0.41);
    width: 36px;
    color: #fff;
    height: 36px;
    border-radius: 3px;
    margin-bottom: 15px;
    margin-right: 15px;
    margin-left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
}

.tb_reel_close_btn {
    position: fixed;
    right: 15px;
    top: 15px;
    background: rgba(0, 0, 0, 0.41);
    width: 36px;
    height: 36px;
    display: flex;
    color: #fff;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    pointer-events: all;
    z-index: 99;
}

.tb_reel_close_btn .tb__icon,
.tb_rp_mute_btn .tb__icon{
    color: #fff;
}

.tb_rp_content{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    width: 100%;
    display: flex;
    align-items: end;
    flex-direction: column;
    padding-bottom: 15px;
}

.tb_rpp_wrap{
    width: 100%;
    margin-bottom: 5px;
}

.tb_rpp_pro {
    display: flex;
    align-items: center;
    justify-content: left;
    background: #FFFFFF;
    padding: 10px;
    border-radius: 6px;
    height: auto;
    margin: 10px;
}

.tb_rpp_pro_img {
    position: relative;
    width: 90px;
    height: 90px;
    margin-right: 12px;
}

.tb_rpp_pro_img img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    object-fit: contain;
    border-radius: 7px;
    margin: 0 !important;
}

.tb_rpp_pro_title {
    font-family: var(--tb-product-font);
    color: var(--tb-product-color);
    font-size: var(--tb-product-size);
    font-weight: var(--tb-product-varient);
    margin-bottom: 7px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.tb_rpp_p_wrap {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    flex-wrap: wrap;
}

.tb_rpp_pro_cont {
    width: calc(100% - 107px);
    text-align: left;
}

.tb_rpp_p {
    color: var(--tb-price-color);
    font-family: var(--tb-price-font);
    font-size: var(--tb-price-size);
    font-weight: var(--tb-price-varient);
    margin-right: 5px;
}

.tb_rpp_p.tb_rpp_d_p{
    transform: scale(.8);
    opacity: .6;
    margin-left: -8px;
    text-decoration: line-through;
}

.tb_rpp_btn {
    color: #FFF;
    font-family: inherit;
    font-size: 12px;
    line-height: normal;
    text-transform: uppercase;
    padding: 8px 15px;
    background: #000;
    display: inline-block;
    position: relative;
    border: none;
    font-family: var(--tb-shop-btn-font);
    color: var(--tb-shop-btn-color);
    font-size: var(--tb-shop-btn-size);
    background-color: var(--tb-shop-btn-bg-color);
}

.tb_rpp_btn_spinner:before {
    content: "";
    position: absolute;
    top: 50%;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    margin-top: -8px;
    border: 1.5px solid #fff;
    border-right: 1.5px solid transparent;
    left: auto;
    right: 8px;
    display: flex;
    animation: tb_btn_animation-spinner 0.5s linear infinite;
}

.tb_rpp_btn_spinner{    
    padding-right: 30px;
}


.tb_rpp_v_spinner {    
    position: absolute;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    width: 100%;
}

.tb_rpp_v_spinner:before {
    content: "";
    position: absolute;
    top: 50%;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    border: 1.5px solid #fff;
    border-right: 1.5px solid transparent;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: flex;
    animation: tb_btn_animation-spinner 0.5s linear infinite;
}


@-webkit-keyframes tb_btn_animation-spinner {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes tb_btn_animation-spinner {
    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.tb_rp_cart_overlay{
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.4);
    z-index: 1;
    opacity: 0;
    transition: all .3s linear;
}

.tb_rp_add_cart_m .tb_rp_cart_overlay{
    opacity: 1;
}

.tb_rp_cart_modal {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    transition: all .3s linear;
    z-index: 2;
    transform: translateY(100%);
}

.tb_rp_add_cart_m .tb_rp_cart_modal{
    transform: translateY(0);
}

.tb_rp_cart_body {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border-radius: 14px 14px 0 0;
    transition: all .3s linear;
    outline: 0;
}

.tb_rp_cart_modal .ts_reel_close_btn{
    position: absolute;
    top: -50px;
}

.tb_rp_cart_wrap {
    position: relative;
    padding: 30px 20px;
}

.tb_rp_cart_wrapper {
    display: flex;
    flex-direction: column;
}

.tb_rp_p_detail {
    width: 100%;
    display: flex;
    align-items: start;
    margin-bottom: 15px;
}

.tb_rp_p_img_ {
    padding-right: 15px;
}

.tb_rp_p_img_ img {
    width: 90px;
    height: 90px;
    display: block;
    border-radius: 8px;
    overflow: hidden;
    object-fit: cover;
    margin: 0 !important;
}

.tb_rp_p_cont_{
    display: flex;
    flex-direction: column;
}

.tb_rp_cp_title {
    font-size: 16px;
    color: #000;
    line-height: 1.4;
    font-weight: 400;
    margin-bottom: 6px;
    font-family: inherit;
    word-break: break-all;
}

.tb_rp_cp_price {
    font-size: 16px;
    line-height: normal;
    font-weight: 600;
    font-family: inherit;
    color: #000;
}

.tb_rp_cp_error {
    background-color: #fcc;
    padding: 6px 15px;
    color: black;
    font-size: 12px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.25s;
    margin-bottom: 15px;
}

.tb_rp_cp_error.tb_rp_cp_error-visible{
    visibility: visible;
    opacity: 1;
}

.tb_rp_cp__pl{
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
}

.tb_rp_cp_op_title {
    font-size: 14px;
    font-weight: 400;
    line-height: normal;
    margin-bottom: 8px;
    font-family: var(--tb-product-font);
}

.tb_rp_cp_op_wrap{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.tb_rp_cp_op_ {
    width: 40px;
    height: 40px;
    border: 1px solid #DEDEDE;
    display: inline-flex;
    margin-bottom: 10px;
    margin-right: 10px;
    padding: 4px;
    overflow: hidden;
}


.tb_rp_cp_tile{
    width: 100%;
    height: 100%;
    background-color: #DEDEDE;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    position: relative;
    color: #000;
}

.tb_rp_cp_op_.tb_rp_cp_op_size{
    width: 70px;
    height: 40px;
    padding: 0;
}

.tb_disabled .tb_rp_cp_tile:after{
    content: '';
    position: absolute;
    width: 120%;
    top: 50%;
    left: -10%;
    height: 1px;
    background-color: #000;
    transform: rotate(151deg);
}

.tb_rp_cp_op_.tb_active {
    border-color: #000;
}

.tb_rp_cp_op_size .tb_rp_cp_tile{
    background-color: #fff;
}
.tb_rp_cp_op_size.tb_disabled .tb_rp_cp_tile{
    background-color: #DEDEDE;
    pointer-events: none;
}

.tb_rp_cp_op_img{
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: contain;
    margin: 0 !important;
}

.tb_rp_cp__action{
    margin-bottom: 20px;
    display: flex;
}
.tb_cart_primary, .tb_cart_secondary {
    text-wrap: wrap;
    align-items: center;
    border: 1px solid #000;
    border-radius: 0;
    cursor: pointer;
    display: flex;
    font-size: 14px;
    font-weight: 500;
    justify-content: center;
    line-height: normal;
    margin-right: 15px;
    min-height: 42px;
    overflow: hidden;
    padding: 8px 12px;
    text-align: center;
    text-overflow: ellipsis;
    text-transform: uppercase;
    transition: all .3s ease-in-out;
}

.tb_cart_secondary {
    background-color: #fff;
    color: #000;
    width: 40%;
}

.tb_cart_primary {
    background-color: #000;
    color: #fff;
    margin-right: 0;
    width: 60%;
}

.tb_rp_checkOut .tb_rp_p_img_{
    padding-right: 0;
    margin-bottom: 20px;
}

.tb_rp_checkOut .tb_rp_p_cont_ {
    text-align: center;
}

.tb_rp_checkOut .tb_rp_cp_title{
    font-size: 18px;
    margin-bottom: 10px;
}

.tb_rp_checkOut .tb_rp_cp_price{
    font-size: 18px;
    margin-bottom: 10px;
}

.tb_rp_checkOut .tb_rp_p_img_ img{
    padding-right: 0;
    height: 140px;
    border-radius: 0px;
    width: 140px;
    margin: 0 !important;
}

.tb_rp_checkOut .tb_rp_p_detail{
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.tb_rp_p_info{
    color: #8f8f8f;
    font-family: inherit;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-bottom: 40px;
    margin-top: 12px;
    text-align: center;
}

.tb_rp_checkOut .tb_rp_cp__action{
    flex-direction: column;
}

.tb_rp_checkOut .tb_cart_secondary,
.tb_rp_checkOut .tb_cart_primary{
    width: 100%;
}

.tb_rp_checkOut .tb_cart_primary{
    margin-bottom: 15px;
}

.tb_rp_media_wrap{
    height: 100vh;
}

.tb_rp_media_holder{
    height: 100%;
}

.tb_rp_s_image{
    height: 100vh;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_rp_media_holder .swiper-pagination{
    position: absolute;
    bottom: 13px;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_rp_bullet{
    height: 8px;
    width: 8px;
    background-color: #fff;
    border-radius: 50%;
    margin: 4px;
    opacity: .5;
}

.tb_rp_bullet.swiper-pagination-bullet-active{
    opacity: 1;
}
@media (min-width: 768px) {
    .tb_cart_primary, .tb_cart_secondary {
        text-wrap: nowrap;
    }
}